import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { history } from '../../helpers/history';
import { connect } from 'react-redux';
import { 
  getDataById, 
  editData
} from '../../redux/data/dataAction';

class FormData extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            datas: []
        }
        this.handleClick = this.handleClick.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    handleCancel = (event) => {
        event.preventDefault();
        history.push('/data');
    }

    handleClick = (event, data) => {
        event.preventDefault();
        this.props.dispatch(editData(data, this.props.id))
        console.log(data, this.props.id)
    };

    handleChange = prop => event => {
        event.preventDefault();
        this.setState({ [prop]: event.target.value })
    }

    async componentWillMount(){
        // const id = this.props.id
        await this.props.dispatch(getDataById(this.props.id))
        console.log(this.props.initialize(this.state.datas))
        // console.log(this.props.dispatch(getDataById(this.props.id)))
    }
  
    async componentWillReceiveProps(newProps){
        this.setState({
          datas: await newProps.datas.datas.datas
        })
    }

    render(){
        const { submitting } = this.props;
        const renderField = ({
            input,
            type,
            id,
            meta: {touched, error}
        }) => (
            // Render schema for checkbox
            (type === 'checkbox')
                ?
                <div className="checkbox">
                    {touched &&
                    (error &&
                    <span className="error-text" style={{ color: 'red' }}>
                    {error}
                  </span>)}
                </div>
                :
                // Render schema for inputs
                <div className="form-group">
                    <input {...input} id={id} type={type} className="form-control"/>
                    {touched &&
                    (error &&
                    <span className="error-text" style={{ color: 'red' }}>
                    {error}
                  </span>)}
                </div>
        );
        console.log(this.props.initialValues)
        return(
            <div className="row">
                <div className="col-sm-12 col-lg-12">
                    <form onSubmit={this.handleClick}>  
                       <div className="row">
                            <div className="col-sm-6 col-lg-6 col-md-6">
                                <label> First Name </label> 
                                <Field 
                                    name="first_name" 
                                    type="text"
                                    component={renderField} 
                                    className="form-control"
                                    defaultValue={this.props.initialValues.first_name}
                                />
                            </div>
                            <div className="col-sm-6 col-lg-6 col-md-6">
                                <label>Last Name</label>
                                <Field 
                                    name="last_name" 
                                    type="text"
                                    component={renderField}
                                    className="form-control" 
                                />
                            </div>
                            <div className="col-sm-12 col-lg-12 col-md-12">
                                <br/>
                                <label>Email</label>
                                <Field 
                                    name="email" 
                                    type="email"
                                    component={renderField}
                                    className="form-control" 
                                />
                            </div>
                            <div className="col-sm-12 col-lg-12 col-md-12">
                                <br/>
                                <label> Gender </label>
                                <Field
                                    type="select" 
                                    name="gender" 
                                    component="select" 
                                    className="form-control" 
                                >
                                    <option> -- Silakan Pilih -- </option>
                                    <option value="Male"> Male </option>
                                    <option value="Female"> Female </option>
                                </Field>
                                <br/>    
                            </div>
                            <div className="col-sm-12 col-lg-12 col-md-12">
                                <label>IP Address</label>
                                <Field 
                                    name="ip_address" 
                                    type="text"
                                    component={renderField}
                                    placeholder="ip address"
                                    className="form-control" 
                                />
                            </div>
                            <div className="col-sm-12 col-lg-12 col-md-12">
                                <br/>
                                <label> Size </label>
                                <Field 
                                    name="size" 
                                    type="select" 
                                    className="form-control"
                                    component="select"
                                >
                                    <option> -- Silakan Pilih -- </option>
                                    <option value="XS"> XS </option>
                                    <option value="S"> S </option>
                                    <option value="M"> M </option>
                                    <option value="L"> L </option>
                                    <option value="XL"> XL </option>
                                    <option value="2XL"> 2XL </option>
                                    <option value="3XL"> 3XL </option>
                                </Field>
                                <br/>
                            </div>
                            <div className="col-sm-12 col-lg-12 col-md-12">
                                <label> Title </label>
                                <Field 
                                    name="title" 
                                    type="select" 
                                    className="form-control"
                                    component="select"
                                >
                                    <option> -- Silakan Pilih -- </option>
                                    <option value="Dr"> Dr </option>
                                    <option value="Honorable"> Honorable </option>
                                    <option value="Mr"> Mr </option>
                                    <option value="Mrs"> Mrs </option>
                                    <option value="Ms"> Ms </option>
                                    <option value="Rev"> Rev </option>
                                </Field>             
                                <br/>   
                            </div>
                            <div className="col-sm-12 col-lg-12 col-md-12">
                                <div style={{ float: 'right' }}>
                                    <button 
                                        type="submit"
                                        disabled={submitting} 
                                        className="btn btn-primary data-submit" 
                                    >
                                        Submit
                                    </button>
                                    &nbsp;
                                    <button
                                        className="btn btn-danger"
                                        onClick={this.handleCancel}
                                    >
                                        Cancel
                                    </button>
                                </div>
                            </div>
                        </div>
                    </form>     
                </div>
            </div>
        )
    }
}

function mapStateToProps(state){
    return({
        initialValues:{ 
            first_name: state.data.datas.datas && state.data.datas.datas.map((data) => { return( data.first_name ) }),
            last_name: state.data.datas.datas && state.data.datas.datas.map((data) => { return( data.last_name ) }),
            email: state.data.datas.datas && state.data.datas.datas.map((data) => { return( data.email ) }),
        },
        datas: state.data,
    });
}

export default connect(mapStateToProps)(reduxForm({
    form: 'FormData',
    enableReinitialize: true,
    keepDirtyOnReinitialize: true,
})(FormData));