import axios from 'axios';
import * as actionTypes from './dataType';
import API from '../../root/';
import { history } from '../../helpers/history';
import swal from 'sweetalert';
// import { push } from 'react-router-redux';

export function fetchData(data){
    return{
        type: actionTypes.GET_DATA_SUCCESS,
        payload: { data }
    }
}

export function fetchDataById(datas){
    return{
        type: actionTypes.GET_DATA_BY_ID_SUCCESS,
        payload: { datas }
    }
}

export function failedFetchData(error){
    return{
        type: actionTypes.GET_DATA_FAILED,
        payload: { error }
    }
}

export function failedFetchDataById(error){
    return{
        type: actionTypes.GET_DATA_BY_ID_FAILED,
        payload: { error }
    }
}

export function failedPostData(error){
    return{
        type: actionTypes.POST_DATA_FAILED,
        payload: { error }
    }
}

export function failedDeleteData(error){
    return{
        type: actionTypes.DELETE_DATA_FAILED,
        payload: { error }
    }
}

export function failedEditData(error){
    return{
        type: actionTypes.EDIT_DATA_FAILED,
        payload: { error }
    }
}

export function getData(){
    return function(dispatch){
        dispatch({ type: actionTypes.GET_DATA_REQUEST, loading: true })
        const URL = API+`get/data/`
        return axios.get(URL)
            .then((response) => {
                dispatch(fetchData(response.data))
            })
            .catch((error) => {
                dispatch(failedFetchData(error))
            })
    }
}

export function getDataById(id){
    return function(dispatch){
        dispatch({ type: actionTypes.GET_DATA_BY_ID_REQUEST, loading: true })
        const URL = API+`get/data/${id}`
        return axios.get(URL)
            .then((response) => { 
                dispatch(fetchDataById(response.data))
            })
            .catch((error) => {
                dispatch(failedFetchDataById(error))
            })
    }
}

export function createData(data){
    return function(dispatch){
        dispatch({ type: actionTypes.POST_DATA_REQUEST, loading: true })
        const URL = API+`post/data/`;
        return axios.post(URL, data)
            .then((response) => {
                dispatch({ type: actionTypes.POST_DATA_SUCCESS, payload: response.data })
                swal({
                  icon: 'success',
                  title: 'Berhasil Tambah Data',
                  button: true,
                })
                history.push('/data')
            })
            .catch((error) => {
                dispatch(failedPostData(error))
            })
    }
}

export function deleteData(id){
    return function(dispatch){
        dispatch({ type: actionTypes.DELETE_DATA_REQUEST, loading: true })
        const URL = API+`delete/data/${id}`
        return axios.delete(URL)
            .then((response) => {
                dispatch({ type: actionTypes.DELETE_DATA_SUCCESS, success: true })
                dispatch(getData())
            })
            .catch((error) => {
                dispatch(failedDeleteData(error))
            })
    }
}

export function editData(data){
    return function(dispatch){
        dispatch({ type: actionTypes.EDIT_DATA_REQUEST, loading: true })
        const URL = API+`edit/data/`
        return axios.post(URL, data)
            .then((response) => {
                swal({
                    icon: 'success',
                    title: 'Berhasil Edit Data',
                    button: true,
                })
                history.push('/data')
            })
            .catch((error) => {
                dispatch(failedEditData(error))
            })
    }
}