import React, { Component } from 'react';
import { 
  Button,
  Card, 
  CardBody, 
  CardHeader, 
  Col, 
  Row, 
  Fade,
  Collapse,
} from 'reactstrap';
// import swal from 'sweetalert';
// import { Link } from 'react-router-dom';
// import DataForm from '../../components/Form/FormData'
import FieldArraysForm from '../../components/Form/FormDataArray';
// import { Redirect } from 'react-router';
import { connect } from 'react-redux';
// import { bindActionCreators } from 'redux';
// import { 
//   getDataById, 
//   editData 
// } from '../../redux/data/dataAction';

class Tables extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
        collapse: true,
        fadeIn: true,
        timeout: 300,
        idForm: this.props.match.params.id
    };
    // this.handleClick = this.handleClick.bind(this);
  }

  // handleClick = (event, data) => {
  //   event.preventDefault();
  //   this.props.dispatch(editData(data))
  // };

  // handleChange = prop => event => {
  //   this.setState({ [prop]: event.target.value })
  // }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }

  // async componentWillMount(){
  //   // this.setState({ idForm: this.props.match.params })
  //   const id = this.props.match.params.id;
  //   await this.props.dispatch(getDataById(id))
  //   console.log(this.props.match.params.id)
  // }
  
  // async componentWillReceiveProps(newProps){
  //   this.setState({
  //     datas: await newProps.data.data
  //   })
  //   console.log(this.state.datas && this.state.datas.map((data) => { return ( data.email ) }))
  // }

  render() {
    // const { data } = this.props.data;
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12">
            <Fade timeout={this.state.timeout} in={this.state.fadeIn}>
              <Card>
                <CardHeader>
                  <i className="fa fa-edit"></i> Edit Data
                  <div className="card-header-actions">
                    <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                    <Button color="link" className="card-header-action btn-minimize" data-target="#collapseExample" onClick={this.toggle}><i className="icon-arrow-up"></i></Button>
                  </div>
                </CardHeader>
                <Collapse isOpen={this.state.collapse} id="collapseExample">
                  <CardBody>
                    <FieldArraysForm id={this.state.idForm} />
                  </CardBody>
                </Collapse>
              </Card>
            </Fade>
          </Col>
        </Row>
      </div>

    );
  }
}


const mapStateToProps = (state) => {
    return {
        dataForm: state.form.data,
        // data: state.data.data
    };
}

// const mapDispatchToProps = (dispatch, props) => {
//   const id = props.match.params;
//   return {
//     onGetData: () => dispatch(getDataById(id))
//   };
// }

export default connect(mapStateToProps)(Tables);